/**
 * Use this file for JavaScript code that you want to run in the front-end
 * on posts/pages that contain this block.
 *
 * When this file is defined as the value of the `viewScript` property
 * in `block.json` it will be enqueued on the front end of the site.
 *
 * Example:
 *
 * ```js
 * {
 *   "viewScript": "file:./view.js"
 * }
 * ```
 *
 * If you're not making any changes to this file because your project doesn't need any
 * JavaScript running in the front-end, then you should delete this file and remove
 * the `viewScript` property from `block.json`.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-metadata/#view-script
 */
// display the content from edit
import { registerBlockType } from '@wordpress/blocks';
import { __ } from '@wordpress/i18n';
import { useBlockProps } from '@wordpress/block-editor';

registerBlockType( 'create-block/wptom-animated-call-to-action', {
	apiVersion: 2,
	title: __( 'Animated Call to Action', 'wptom-animated-call-to-action' ),
	icon: 'welcome-add-page',
	category: 'layout',
	keywords: [ __( 'cta' ), __( 'call to action' ), __( 'animated' ) ],
	edit: () => {
		const blockProps = useBlockProps();
		return (
			<div { ...blockProps }>
				<p>Animated Call to Action block</p>
			</div>
		);
	},
	save: () => {
		const blockProps = useBlockProps.save();
		return (
			<div { ...blockProps }>
				<p>Animated Call to Action block</p>
			</div>
		);
	},
} );

/* eslint-disable no-console */
console.log(
	'Hello World! (from create-block-wptom-animated-call-to-action block)'
);
/* eslint-enable no-console */
